.chat {
    flex: 2;
    border-left:1px solid #dddddd35; 
    border-right:1px solid #dddddd35; 
    height: 100%;
    display: flex;
    flex-direction: column;

    .top {
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom:1px solid #dddddd35; 

        .user {
            display: flex;
            align-items: center;
            gap: 20px;
            
            img {
                width: 60px;
                height: 60px;
                border-radius: 50%;
                object-fit: cover;
            }
            
            .texts {
                display: flex;
                flex-direction: column;
                gap: 5px;

                span {
                    font-size: 18px;
                    font-weight: bold;
                }

                p {
                    font-size: 14px;
                    font-weight: 300;
                    color: #a5a5a5
                }
            }
        }

        .icons {
            display: flex;
            gap: 20px;

            img {
                width: 20px;
                height: 20px;
                cursor: pointer;
            }
        }
    }

    .center:hover {
        overflow-y: scroll; 
    }
    
    
    .center::-webkit-scrollbar {
        width: 1px;
    }
    
    .center::-webkit-scrollbar-track {
        background: transparent;
    }
    
    .center::-webkit-scrollbar-thumb {
        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 10px;
    }
    
    .center:hover::-webkit-scrollbar-thumb {
        background-color: rgba(255, 255, 255, 0.6); 
    }

    .center {
        padding: 20px;
        flex: 1;
        overflow-y: scroll;
        overflow-x: hidden;
        scrollbar-width: none; 
        -ms-overflow-style: none;
        display: flex;
        flex-direction: column;
        gap: 20px;
        
        .message {
            max-width: 70%;
            display: flex;
            gap: 20px;

        &.own {
            align-self: flex-end;

            .texts {
                p {
                    background-color: #5183fe;
                }
            }
        }
        img {
            width: 30px;
            height: 30px;
            border-radius: 50%;
            object-fit: cover;
        }

        .texts {
            flex: 1;
            display: flex;
            flex-direction: column;
            gap: 5px;

            img {
                width: 100%;
                height: 300px;
                border-radius: 10px;
                object-fit: cover;
            }

            p {
                padding: 20px;
                background-color: rgba(17, 25, 40, 0.3);
                border-radius: 10px;
            }

            span {
                font-size: 13px;
            }
        }
    }
    }

    .bottom {
        padding: 18px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-top:1px solid #dddddd35; 
        gap: 20px;
        margin-top: auto;

        .icons {
            display: flex;
            align-items: center;
            gap: 20px;
        }

        img {
            width: 20px;
            height: 20px;
            cursor: pointer;
        }

        input {
            flex: 1;
            background-color: transparent;
            border: none;
            outline: none;
            color: #fff;
            padding: 20px;
            border-radius: 10px;
            font-size: 16px;
        }

        .emoji {
            position: relative;

            .picker {
                position: absolute;
                bottom: 50px;
                left: 0;
            }
        }
        
        .sendButton {
            background-color: #5183fe;
            padding: 10px 20px;
            color: #fff;
            border-radius: 5px;
            border: none;
            cursor: pointer;
        }
    }
}